.mainConstructor {
    margin: auto;
    display: flex;
    width: 80%;
    gap: 40px;
    justify-content: center;
}

.mainProfile {
    max-width: 80%;
    display: flex;
    justify-content: center;
    margin-top: 7%;
}

.main {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.link,
.link:link,
.link:active {
    color: #4c4cff;
    text-decoration: none;
    cursor: pointer;
}

.button {
    display: flex;
    justify-content: center;
}

.text {
    text-align: center;
}

section {
    margin: auto;
    /* width: 35%; */
    text-align: start;
}

.feed_show_order_section {
    margin: auto;
    width: 35%;
    text-align: start;
}

.profile_shared_main {
    max-width: 80%;
    width: 80%;
    margin: 0 auto;
    margin-top: 100px;
    display: flex;
    justify-content: center;
}